import React from 'react';

const Footer = ({ setActiveTab }) => {
  return (
    <footer className="app-footer">
      <div className="footer-wave">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
          <path fill="#2c3e50" fillOpacity="1" d="M0,96L48,112C96,128,192,160,288,186.7C384,213,480,235,576,224C672,213,768,171,864,165.3C960,160,1056,192,1152,197.3C1248,203,1344,181,1392,170.7L1440,160L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path>
        </svg>
      </div>
      
      <div className="footer-content">
        <div className="container">
          <div className="footer-grid">
            <div className="footer-about">
              <div className="footer-logo">
                <img src="/logo192.png" alt="DRIVE Logo" className="footer-logo-img" />
                <span className="footer-logo-text">Project DRIVE</span>
              </div>
              <p className="footer-description">
                Revolutionizing urban mobility with AI-powered traffic management using Vehicular Ad Hoc Networks and P2P communication.
              </p>
              <div className="footer-social">
                <a href="#" className="social-link" aria-label="Twitter">
                  <span className="social-icon">𝕏</span>
                </a>
                <a href="#" className="social-link" aria-label="LinkedIn">
                  <span className="social-icon">in</span>
                </a>
                <a href="#" className="social-link" aria-label="Facebook">
                  <span className="social-icon">f</span>
                </a>
                <a href="#" className="social-link" aria-label="Instagram">
                  <span className="social-icon">📷</span>
                </a>
              </div>
            </div>
            
            <div className="footer-links-section">
              <h4>Quick Links</h4>
              <ul className="footer-links">
                <li><button onClick={() => setActiveTab('home')}>Home</button></li>
                <li><button onClick={() => setActiveTab('about')}>About</button></li>
                <li><button onClick={() => setActiveTab('features')}>Features</button></li>
                <li><button onClick={() => setActiveTab('demo')}>Demo</button></li>
                <li><button onClick={() => setActiveTab('contact')}>Contact</button></li>
              </ul>
            </div>
            
            <div className="footer-features-section">
              <h4>Key Features</h4>
              <ul className="footer-features">
                <li>VANET Technology</li>
                <li>AI-Powered Analysis</li>
                <li>P2P Communication</li>
                <li>Smart Parking</li>
                <li>Enhanced Road Safety</li>
              </ul>
            </div>
            
            <div className="footer-contact">
              <h4>Contact Us</h4>
              <div className="contact-info">
                <div className="contact-item">
                  <span className="contact-icon">📍</span>
                  <span>123 Innovation Street, New Delhi, India</span>
                </div>
                <div className="contact-item">
                  <span className="contact-icon">📞</span>
                  <span>+91 1234567890</span>
                </div>
                <div className="contact-item">
                  <span className="contact-icon">✉️</span>
                  <span>info@projectdrive.ai</span>
                </div>
              </div>
              <button 
                className="footer-cta-button" 
                onClick={() => setActiveTab('contact')}
              >
                Get In Touch
              </button>
            </div>
          </div>
        </div>
      </div>
      
      <div className="copyright">
        <div className="container">
          <div className="copyright-content">
            <p>&copy; {new Date().getFullYear()} Project DRIVE - Turning Congestion into Coordination!</p>
            <div className="copyright-links">
              <a href="#">Privacy Policy</a>
              <span className="divider">|</span>
              <a href="#">Terms of Service</a>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer; 