import React, { useState, useEffect, createContext } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Header from './components/Header';
import Footer from './components/Footer';
import HomePage from './components/HomePage';
import AboutPage from './components/AboutPage';
import FeaturesPage from './components/FeaturesPage';
import DemoPage from './components/DemoPage';
import ContactPage from './components/ContactPage';
import LoadingScreen from './components/LoadingScreen';
import LogoUploader from './components/LogoUploader';
import './styles.css';

// Create context for logo management
export const LogoContext = createContext();

function App() {
  const [activeTab, setActiveTab] = useState('home');
  const [loading, setLoading] = useState(true);
  const [loadingProgress, setLoadingProgress] = useState(0);
  const [pageTransitioning, setPageTransitioning] = useState(false);
  const [currentLogo, setCurrentLogo] = useState(`${process.env.PUBLIC_URL}/assets/drivelogo.jpg`);

  // Logo management function
  const updateLogo = (logoPath) => {
    setCurrentLogo(logoPath);
  };
  
  // Logo context value
  const logoContextValue = {
    currentLogo,
    updateLogo
  };

  useEffect(() => {
    document.title = 'Project DRIVE - AI Traffic Management';
    
    // Handle URL hash for direct navigation
    const hash = window.location.hash.replace('#', '');
    if (hash && ['home', 'about', 'features', 'demo', 'contact'].includes(hash)) {
      setActiveTab(hash);
    }
    
    // Update hash when tab changes
    const handleHashChange = () => {
      const newHash = window.location.hash.replace('#', '');
      if (newHash && ['home', 'about', 'features', 'demo', 'contact'].includes(newHash)) {
        setActiveTab(newHash);
      }
    };
    
    window.addEventListener('hashchange', handleHashChange);

    // Add scroll listener for header styling
    const handleScroll = () => {
      const header = document.querySelector('.app-header');
      if (header) {
        if (window.scrollY > 50) {
          header.classList.add('scrolled');
        } else {
          header.classList.remove('scrolled');
        }
      }
    };

    window.addEventListener('scroll', handleScroll);

    // Simulate advanced loading with progress
    const simulateLoading = () => {
      let progress = 0;
      const interval = setInterval(() => {
        progress += Math.floor(Math.random() * 10) + 1;
        
        if (progress >= 100) {
          progress = 100;
          setLoadingProgress(progress);
          clearInterval(interval);
          
          // Add a small delay at 100% before hiding the loading screen
          setTimeout(() => {
            setLoading(false);
          }, 500);
        } else {
          setLoadingProgress(progress);
        }
      }, 150);
    };
    
    simulateLoading();

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('hashchange', handleHashChange);
    };
  }, []);
  
  // Update URL hash when tab changes
  useEffect(() => {
    window.location.hash = activeTab;
    setPageTransitioning(true);
    
    // Scroll to top on page change
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
    
    // Reset page transitioning state after animation completes
    const transitionTimeout = setTimeout(() => {
      setPageTransitioning(false);
    }, 600);
    
    return () => clearTimeout(transitionTimeout);
  }, [activeTab]);

  const handleSetActiveTab = (tab) => {
    if (tab !== activeTab) {
      setPageTransitioning(true);
      setActiveTab(tab);
    }
  };

  const renderContent = () => {
    switch(activeTab) {
      case 'home':
        return <HomePage setActiveTab={handleSetActiveTab} />;
      case 'about':
        return <AboutPage />;
      case 'features':
        return <FeaturesPage />;
      case 'demo':
        return <DemoPage />;
      case 'contact':
        return <ContactPage />;
      default:
        return <HomePage setActiveTab={handleSetActiveTab} />;
    }
  };

  // Page transition variants
  const pageVariants = {
    initial: {
      opacity: 0,
      y: 20
    },
    animate: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        ease: [0.6, -0.05, 0.01, 0.99],
        when: "beforeChildren",
        staggerChildren: 0.1
      }
    },
    exit: {
      opacity: 0,
      y: -20,
      transition: {
        duration: 0.4,
        ease: [0.6, -0.05, 0.01, 0.99]
      }
    }
  };

  // Page container
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { 
        duration: 0.5,
        delay: 0.3,
        ease: "easeInOut"
      }
    },
    exit: { 
      opacity: 0,
      transition: { duration: 0.3, ease: "easeOut" }
    }
  };

  if (loading) {
    return <LoadingScreen progress={loadingProgress} />;
  }

  return (
    <LogoContext.Provider value={logoContextValue}>
      <motion.div 
        className="app-container"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        exit="exit"
      >
        <Header activeTab={activeTab} setActiveTab={handleSetActiveTab} pageTransitioning={pageTransitioning} />
        
        <AnimatePresence mode="wait">
          <motion.main 
            key={activeTab}
            className="main-content"
            initial="initial"
            animate="animate"
            exit="exit"
            variants={pageVariants}
            onAnimationComplete={() => setPageTransitioning(false)}
          >
            {renderContent()}
          </motion.main>
        </AnimatePresence>

        <Footer setActiveTab={handleSetActiveTab} />
        
        {/* Logo Uploader Component */}
        <LogoUploader />
        
        {/* Page transition overlay */}
        <AnimatePresence>
          {pageTransitioning && (
            <motion.div 
              className="page-transition-overlay"
              initial={{ scaleY: 0, originY: 0 }}
              animate={{ 
                scaleY: [0, 1, 1, 0],
                originY: [0, 0, 1, 1],
              }}
              exit={{ scaleY: 0, originY: 1 }}
              transition={{ 
                duration: 1, 
                times: [0, 0.4, 0.6, 1],
                ease: "easeInOut"
              }}
            />
          )}
        </AnimatePresence>
      </motion.div>
    </LogoContext.Provider>
  );
}

export default App;