import React, { useEffect } from 'react';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const AboutPage = () => {
  // Intersection observer hooks for scroll animations
  const [titleRef, titleInView] = useInView({ 
    threshold: 0.1,
    triggerOnce: true
  });
  
  const [textRef, textInView] = useInView({ 
    threshold: 0.2,
    triggerOnce: true
  });
  
  const [techRef, techInView] = useInView({ 
    threshold: 0.2,
    triggerOnce: true
  });

  // Set document title
  useEffect(() => {
    document.title = 'About | Project DRIVE';
  }, []);

  // Animation variants
  const titleVariants = {
    hidden: { opacity: 0, y: -50 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { 
        duration: 0.8, 
        ease: [0.6, -0.05, 0.01, 0.99] 
      }
    }
  };

  const textVariants = {
    hidden: { opacity: 0, x: -50 },
    visible: { 
      opacity: 1, 
      x: 0,
      transition: { 
        duration: 0.6, 
        ease: "easeOut"
      }
    }
  };

  const techStackVariants = {
    hidden: { opacity: 0, x: 50 },
    visible: { 
      opacity: 1, 
      x: 0,
      transition: { 
        duration: 0.6, 
        ease: "easeOut" 
      }
    }
  };

  const tableRowVariants = {
    hidden: { opacity: 0, x: 20 },
    visible: i => ({ 
      opacity: 1, 
      x: 0,
      transition: { 
        delay: i * 0.1,
        duration: 0.4
      }
    })
  };

  const listItemVariants = {
    hidden: { opacity: 0, x: -20 },
    visible: i => ({ 
      opacity: 1, 
      x: 0,
      transition: { 
        delay: i * 0.1,
        duration: 0.4
      }
    })
  };

  return (
    <div className="about-page">
      <motion.div 
        className="page-header"
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <div className="container">
          <motion.h1
            ref={titleRef}
            variants={titleVariants}
            initial="hidden"
            animate={titleInView ? "visible" : "hidden"}
          >
            About Project DRIVE
          </motion.h1>
          <motion.div 
            className="header-line"
            initial={{ width: 0 }}
            animate={{ width: "100px" }}
            transition={{ duration: 0.8, delay: 0.4 }}
          />
        </div>
      </motion.div>

      <div className="about-content container">
        <div className="about-grid">
          <motion.div 
            className="about-text"
            ref={textRef}
            variants={textVariants}
            initial="hidden"
            animate={textInView ? "visible" : "hidden"}
          >
            <motion.p
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.2, duration: 0.8 }}
            >
              Project DRIVE is an AI-powered, decentralized traffic management system designed to optimize urban mobility by leveraging Vehicular Ad Hoc Networks (VANETs) and Peer-to-Peer (P2P) communication.
            </motion.p>
            
            <motion.p
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.3, duration: 0.8 }}
            >
              Our solution addresses critical issues in metropolitan cities:
            </motion.p>
            
            <motion.ul>
              {[
                "Severe traffic congestion causing delays in commuting",
                "Increased pollution due to fuel wastage from idling vehicles",
                "Road safety hazards leading to frequent accidents",
                "Inefficient traffic management without real-time coordination"
              ].map((item, i) => (
                <motion.li
                  key={i}
                  custom={i}
                  variants={listItemVariants}
                  initial="hidden"
                  animate={textInView ? "visible" : "hidden"}
                >
                  {item}
                </motion.li>
              ))}
            </motion.ul>
            
            <motion.p
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.6, duration: 0.8 }}
            >
              Using cutting-edge technology, we're turning congestion into coordination!
            </motion.p>
          </motion.div>
          
          <motion.div 
            className="tech-stack"
            ref={techRef}
            variants={techStackVariants}
            initial="hidden"
            animate={techInView ? "visible" : "hidden"}
          >
            <motion.h3
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.2, duration: 0.5 }}
            >
              Technology Stack
            </motion.h3>
            
            <motion.div 
              className="tech-table-container"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.3, duration: 0.5 }}
            >
              <table className="tech-table">
                <tbody>
                  {[
                    { label: "Backend", value: "Python 3.x, Flask" },
                    { label: "Machine Learning", value: "YOLOv4-tiny, OpenCV, TensorFlow" },
                    { label: "Frontend", value: "React.js, CSS" },
                    { label: "Database", value: "MongoDB, PostgreSQL" },
                    { label: "Communication", value: "IEEE 802.11p, DSRC" }
                  ].map((row, i) => (
                    <motion.tr
                      key={i}
                      custom={i}
                      variants={tableRowVariants}
                      initial="hidden"
                      animate={techInView ? "visible" : "hidden"}
                    >
                      <td><strong>{row.label}</strong></td>
                      <td>{row.value}</td>
                    </motion.tr>
                  ))}
                </tbody>
              </table>
            </motion.div>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default AboutPage; 