import React, { useState, useEffect, useContext } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { LogoContext } from '../App';

const Header = ({ activeTab, setActiveTab }) => {
  const [scrolled, setScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const { currentLogo, updateLogo } = useContext(LogoContext);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleMobileMenu = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };

  // Toggle between available logos
  const toggleLogo = () => {
    const newLogo = currentLogo.includes('drivelogo.jpg') 
      ? `${process.env.PUBLIC_URL}/assets/impactlogo.jpg` 
      : `${process.env.PUBLIC_URL}/assets/drivelogo.jpg`;
    updateLogo(newLogo);
  };

  // Header animation variants
  const headerVariants = {
    hidden: { y: -100, opacity: 0 },
    visible: { 
      y: 0, 
      opacity: 1,
      transition: { 
        type: "spring", 
        stiffness: 100, 
        damping: 15,
        mass: 1
      }
    }
  };

  // Logo animation variants
  const logoVariants = {
    normal: { scale: 1 },
    hover: { 
      scale: 1.05,
      transition: { duration: 0.3, yoyo: Infinity, ease: "easeInOut" }
    }
  };

  // Navigation item variants for staggered animation
  const navItemVariants = {
    hidden: { opacity: 0, y: -20 },
    visible: i => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: i * 0.1 + 0.3,
        duration: 0.5,
        ease: [0.6, -0.05, 0.01, 0.99]
      }
    }),
    hover: { 
      y: -3,
      transition: { duration: 0.2 } 
    }
  };

  // Mobile menu variants
  const mobileMenuVariants = {
    closed: { 
      opacity: 0,
      height: 0,
      transition: {
        duration: 0.3,
        ease: "easeInOut",
        when: "afterChildren"
      }
    },
    open: { 
      opacity: 1,
      height: "auto",
      transition: {
        duration: 0.3,
        ease: "easeInOut",
        when: "beforeChildren",
        staggerChildren: 0.1
      }
    }
  };

  return (
    <motion.header 
      className={`app-header ${scrolled ? 'scrolled' : ''}`}
      variants={headerVariants}
      initial="hidden"
      animate="visible"
    >
      <div className="container header-container">
        <motion.div 
          className="logo-container"
          whileHover="hover"
          variants={logoVariants}
          onClick={toggleLogo}
        >
          <motion.img 
            src={currentLogo}
            alt="DRIVE Logo" 
            className="header-logo"
            initial={{ rotate: -5 }}
            animate={{ rotate: 0 }}
            transition={{ duration: 0.5, ease: "easeOut" }}
          />
          <div className="logo-text-container">
            <motion.span 
              className="logo-text"
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.2, duration: 0.5 }}
            >
              Project <motion.span 
                className="drive-text"
                initial={{ color: "#3a0ca3" }}
                animate={{ color: "#4361ee" }}
                transition={{ duration: 2, repeat: Infinity, repeatType: "reverse" }}
              >DRIVE</motion.span>
            </motion.span>
            <motion.span 
              className="logo-tagline"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.4, duration: 0.5 }}
            >
              AI-Powered Traffic Management
            </motion.span>
          </div>
        </motion.div>

        <motion.button 
          className="mobile-menu-toggle" 
          onClick={toggleMobileMenu}
          whileTap={{ scale: 0.9 }}
        >
          <motion.span 
            className={`menu-icon ${mobileMenuOpen ? 'open' : ''}`}
            animate={mobileMenuOpen ? { rotate: 135 } : { rotate: 0 }}
            transition={{ duration: 0.3 }}
          ></motion.span>
        </motion.button>

        <AnimatePresence>
          <motion.nav 
            className={`main-nav ${mobileMenuOpen ? 'mobile-open' : ''}`}
            variants={mobileMenuVariants}
            initial={false}
            animate={mobileMenuOpen ? "open" : "closed"}
          >
            <ul className="nav-list">
              {[
                { id: 'home', icon: '🏠', label: 'Home' },
                { id: 'about', icon: 'ℹ️', label: 'About' },
                { id: 'features', icon: '✨', label: 'Features' },
                { id: 'demo', icon: '🚦', label: 'Demo' }
              ].map((item, i) => (
                <motion.li 
                  key={item.id}
                  custom={i}
                  variants={navItemVariants}
                  initial="hidden"
                  animate="visible"
                  whileHover="hover"
                  className={`nav-item ${activeTab === item.id ? 'active' : ''}`}
                >
                  <motion.button 
                    onClick={() => {setActiveTab(item.id); setMobileMenuOpen(false);}}
                    whileTap={{ scale: 0.95 }}
                  >
                    <motion.span 
                      className="nav-icon"
                      animate={activeTab === item.id ? { scale: 1.2 } : { scale: 1 }}
                    >
                      {item.icon}
                    </motion.span>
                    <span>{item.label}</span>
                  </motion.button>
                </motion.li>
              ))}
              <motion.li 
                className="nav-item highlight"
                custom={4}
                variants={navItemVariants}
                initial="hidden"
                animate="visible"
                whileHover="hover"
              >
                <motion.button 
                  className="cta-nav-button" 
                  onClick={() => {setActiveTab('contact'); setMobileMenuOpen(false);}}
                  whileHover={{ 
                    scale: 1.05,
                    boxShadow: "0 5px 15px rgba(67, 97, 238, 0.3)"
                  }}
                  whileTap={{ scale: 0.95 }}
                >
                  <motion.span 
                    className="nav-icon"
                    animate={{ rotate: [0, 5, 0, -5, 0] }}
                    transition={{ duration: 0.5, repeat: Infinity, repeatDelay: 3 }}
                  >
                    📞
                  </motion.span>
                  <span>Contact</span>
                </motion.button>
              </motion.li>
            </ul>
          </motion.nav>
        </AnimatePresence>
      </div>
    </motion.header>
  );
};

export default Header; 