import React, { useEffect, useRef } from 'react';
import { motion, useAnimation, useScroll, useTransform } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import gsap from 'gsap';

const HomePage = ({ setActiveTab }) => {
  // Animation controls for scroll-based animations
  const controls = useAnimation();
  
  // Reference for the traffic animation section
  const trafficRef = useRef(null);
  
  // References for the stat counters
  const statsRef = useRef([]);
  
  // Scroll progress for parallax effects
  const { scrollYProgress } = useScroll();
  const heroY = useTransform(scrollYProgress, [0, 0.2], [0, -50]);
  const heroOpacity = useTransform(scrollYProgress, [0, 0.2], [1, 0.5]);
  
  // Intersection observer hooks for triggered animations
  const [featuresRef, featuresInView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });
  
  const [statsSectionRef, statsInView] = useInView({
    triggerOnce: true,
    threshold: 0.25,
  });
  
  const [processRef, processInView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });
  
  // GSAP animation for traffic simulation
  useEffect(() => {
    if (trafficRef.current) {
      // Create traffic animation timeline
      const tl = gsap.timeline({ repeat: -1 });
      
      // Car 1 animation
      tl.to(".car-1", {
        x: "150%",
        duration: 4,
        ease: "power1.inOut"
      }, 0);
      
      // Car 2 animation
      tl.to(".car-2", {
        x: "-150%",
        duration: 3,
        ease: "power1.inOut"
      }, 1);
      
      // Car 3 animation
      tl.to(".car-3", {
        y: "120%",
        duration: 5,
        ease: "power1.inOut"
      }, 0.5);
      
      // Traffic light animation
      tl.to(".traffic-light", {
        backgroundColor: "#ff0000",
        duration: 0.5
      }, 2)
      .to(".traffic-light", {
        backgroundColor: "#ffff00",
        duration: 0.5
      }, 4)
      .to(".traffic-light", {
        backgroundColor: "#00ff00",
        duration: 0.5
      }, 4.5);
    }
    
    // Start feature animations when in view
    if (featuresInView) {
      controls.start('visible');
    }
    
    // Animate stats counter when in view
    if (statsInView) {
      statsRef.current.forEach((stat, index) => {
        const value = parseInt(stat.dataset.value);
        const duration = 2;
        
        gsap.fromTo(stat, 
          { innerText: 0 }, 
          {
            innerText: value,
            duration: duration,
            delay: index * 0.2,
            ease: "power2.out",
            snap: { innerText: 1 },
            onUpdate: function() {
              stat.innerText = Math.ceil(this.targets()[0].innerText) + '%';
            }
          }
        );
      });
    }
    
    // Process steps animation
    if (processInView) {
      gsap.to(".process-step", {
        opacity: 1,
        y: 0,
        stagger: 0.2,
        duration: 0.8,
        ease: "power2.out"
      });
      
      gsap.to(".process-connector", {
        width: "100%",
        stagger: 0.2,
        duration: 0.8,
        delay: 0.4,
        ease: "power2.inOut"
      });
    }
  }, [controls, featuresInView, statsInView, processInView]);
  
  // Variants for staggered animations
  const featureCardVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: i => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: i * 0.1,
        duration: 0.6,
        ease: [0.6, -0.05, 0.01, 0.99]
      }
    })
  };
  
  // Text reveal animation
  const textRevealVariants = {
    hidden: { y: 100, opacity: 0 },
    visible: { 
      y: 0, 
      opacity: 1,
      transition: { 
        duration: 0.8, 
        ease: [0.6, -0.05, 0.01, 0.99]
      }
    }
  };

  const heroVisualVariants = {
    hidden: { opacity: 0, scale: 0.8 },
    visible: { 
      opacity: 1, 
      scale: 1,
      transition: { 
        duration: 0.8,
        delay: 0.3,
        ease: "easeOut" 
      }
    }
  };

  return (
    <div className="home-page">
      {/* Hero Section with Enhanced Visuals */}
      <motion.section 
        className="hero-section"
        style={{ y: heroY, opacity: heroOpacity }}
      >
        <div className="hero-overlay"></div>
        <div className="hero-particles">
          <div className="tech-particle"></div>
          <div className="tech-particle"></div>
          <div className="tech-particle"></div>
        </div>
        
        <div className="container hero-content">
          <div className="hero-text">
            <motion.h1 
              className="animated-text"
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, ease: "easeOut" }}
            >
              <motion.div 
                className="text-reveal"
                initial={{ width: 0 }}
                animate={{ width: "100%" }}
                transition={{ duration: 0.8, ease: "easeOut", delay: 0.2 }}
              >
                Revolutionizing
              </motion.div> 
              <motion.div 
                className="text-reveal"
                initial={{ width: 0 }}
                animate={{ width: "100%" }}
                transition={{ duration: 0.8, ease: "easeOut", delay: 0.4 }}
              >
                Urban 
                <motion.span 
                  className="drive-highlight"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ duration: 1, delay: 1 }}
                >
                  Mobility
                </motion.span>
              </motion.div>
            </motion.h1>
            
            <motion.h2
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ duration: 0.6, delay: 0.8 }}
            >
              AI-Powered Traffic Management System
            </motion.h2>
            
            <motion.p
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ duration: 0.6, delay: 1 }}
            >
              Project DRIVE harnesses the power of Vehicular Ad Hoc Networks (VANET) 
              and Artificial Intelligence to create smarter, safer, and more efficient traffic flows. 
              Our solution reduces congestion, minimizes travel time, and improves road safety.
            </motion.p>
            
            <motion.div 
              className="hero-buttons"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 1.2 }}
            >
              <motion.button 
                className="primary-button pulse-animation"
                onClick={() => setActiveTab('features')}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                Explore Features
                <span className="button-icon">→</span>
              </motion.button>
              
              <motion.button 
                className="secondary-button"
                onClick={() => setActiveTab('demo')}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                View Demo
              </motion.button>
            </motion.div>
          </div>
          
          <motion.div 
            className="hero-visual"
            variants={heroVisualVariants}
            initial="hidden"
            animate="visible"
          >
            <div className="traffic-animation">
              <div className="city-grid">
                <div className="city-buildings">
                  {[...Array(6)].map((_, i) => (
                    <motion.div 
                      key={i}
                      className="building"
                      initial={{ height: 0 }}
                      animate={{ height: `${Math.random() * 60 + 40}%` }}
                      transition={{ 
                        duration: 0.8, 
                        delay: i * 0.1 + 0.5,
                        ease: "easeOut"
                      }}
                    />
                  ))}
                </div>
                <div className="roads">
                  <div className="road horizontal"></div>
                  <div className="road vertical"></div>
                  <div className="intersection"></div>
                  
                  {/* Traffic lights */}
                  <div className="traffic-light top">
                    <motion.div 
                      className="light red"
                      animate={{ opacity: [1, 0.3, 0.3, 1] }}
                      transition={{ duration: 5, repeat: Infinity, repeatType: "loop" }}
                    />
                    <motion.div 
                      className="light yellow" 
                      animate={{ opacity: [0.3, 1, 0.3, 0.3] }}
                      transition={{ duration: 5, repeat: Infinity, repeatType: "loop" }}
                    />
                    <motion.div 
                      className="light green"
                      animate={{ opacity: [0.3, 0.3, 1, 0.3] }}
                      transition={{ duration: 5, repeat: Infinity, repeatType: "loop" }}
                    />
                  </div>
                  
                  <div className="traffic-light right">
                    <motion.div 
                      className="light red"
                      animate={{ opacity: [0.3, 0.3, 1, 0.3] }}
                      transition={{ duration: 5, repeat: Infinity, repeatType: "loop" }}
                    />
                    <motion.div 
                      className="light yellow"
                      animate={{ opacity: [0.3, 1, 0.3, 0.3] }}
                      transition={{ duration: 5, repeat: Infinity, repeatType: "loop" }}
                    />
                    <motion.div 
                      className="light green"
                      animate={{ opacity: [1, 0.3, 0.3, 1] }}
                      transition={{ duration: 5, repeat: Infinity, repeatType: "loop" }}
                    />
                  </div>
                </div>
                
                {/* Animated cars */}
                <motion.div 
                  className="car car-red"
                  initial={{ x: '-50%', y: '110%' }}
                  animate={{ 
                    y: ['110%', '60%', '60%', '0%', '-110%'],
                    x: ['-50%', '-50%', '-50%', '-50%', '-50%']
                  }}
                  transition={{ 
                    duration: 8, 
                    repeat: Infinity, 
                    repeatType: "loop",
                    times: [0, 0.3, 0.5, 0.7, 1]
                  }}
                />
                
                <motion.div 
                  className="car car-blue"
                  initial={{ x: '-110%', y: '50%' }}
                  animate={{ 
                    x: ['-110%', '0%', '0%', '60%', '110%'],
                    y: ['50%', '50%', '50%', '50%', '50%']
                  }}
                  transition={{ 
                    duration: 8, 
                    repeat: Infinity, 
                    repeatType: "loop",
                    times: [0, 0.3, 0.5, 0.7, 1]
                  }}
                />
                
                <motion.div 
                  className="car car-yellow"
                  initial={{ x: '50%', y: '-110%' }}
                  animate={{ 
                    y: ['-110%', '0%', '0%', '60%', '110%'],
                    x: ['50%', '50%', '50%', '50%', '50%']
                  }}
                  transition={{ 
                    duration: 10, 
                    repeat: Infinity, 
                    repeatType: "loop",
                    times: [0, 0.3, 0.5, 0.7, 1]
                  }}
                />
                
                {/* AI Traffic Control System visualization */}
                <div className="ai-overlay">
                  <motion.div 
                    className="ai-scan-line"
                    animate={{ 
                      y: ['-50%', '100%', '-50%'],
                      opacity: [0, 1, 0]
                    }}
                    transition={{ 
                      duration: 3, 
                      repeat: Infinity, 
                      repeatType: "loop",
                      ease: "linear"
                    }}
                  />
                  
                  <motion.div 
                    className="data-point point-1"
                    animate={{ 
                      scale: [1, 1.5, 1],
                      opacity: [0.7, 1, 0.7]
                    }}
                    transition={{ 
                      duration: 2, 
                      repeat: Infinity, 
                      repeatType: "mirror"
                    }}
                  />
                  
                  <motion.div 
                    className="data-point point-2"
                    animate={{ 
                      scale: [1, 1.5, 1],
                      opacity: [0.7, 1, 0.7]
                    }}
                    transition={{ 
                      duration: 2.5, 
                      repeat: Infinity, 
                      repeatType: "mirror"
                    }}
                  />
                  
                  <motion.div 
                    className="data-point point-3"
                    animate={{ 
                      scale: [1, 1.5, 1],
                      opacity: [0.7, 1, 0.7]
                    }}
                    transition={{ 
                      duration: 1.8, 
                      repeat: Infinity, 
                      repeatType: "mirror"
                    }}
                  />
                  
                  <motion.div 
                    className="route-optimization"
                    initial={{ pathLength: 0, opacity: 0 }}
                    animate={{ 
                      pathLength: 1,
                      opacity: [0, 0.8, 0]
                    }}
                    transition={{ 
                      duration: 4, 
                      repeat: Infinity,
                      repeatType: "loop",
                      ease: "easeInOut"
                    }}
                  />
                </div>
                
                {/* Project DRIVE logo overlay */}
                <motion.div 
                  className="project-badge"
                  initial={{ opacity: 0, scale: 0 }}
                  animate={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.5, delay: 1.5 }}
                >
                  <div className="badge-content">
                    <span className="badge-label">DRIVE</span>
                    <span className="badge-sublabel">AI-Powered</span>
                  </div>
                </motion.div>
              </div>
            </div>
            
            <div className="visual-caption">
              <motion.span
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1.2, duration: 0.8 }}
              >
                AI-Powered Traffic Management Visualization
              </motion.span>
            </div>
          </motion.div>
        </div>
        
        <motion.div 
          className="scroll-indicator"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 2, duration: 1 }}
          onClick={() => {
            document.querySelector('#features').scrollIntoView({ 
              behavior: 'smooth' 
            });
          }}
        >
          <div className="mouse">
            <motion.div 
              className="wheel"
              animate={{ 
                y: [0, 10, 0],
              }}
              transition={{ 
                duration: 1.5, 
                repeat: Infinity, 
                repeatType: "loop" 
              }}
            ></motion.div>
          </div>
          <div className="arrow-scroll">
            <motion.span
              animate={{ 
                y: [0, 5, 0],
                opacity: [0.5, 1, 0.5]
              }}
              transition={{ 
                duration: 1.5, 
                repeat: Infinity, 
                repeatType: "loop",
                delay: 0.3
              }}
            ></motion.span>
            <motion.span
              animate={{ 
                y: [0, 5, 0],
                opacity: [0.5, 1, 0.5]
              }}
              transition={{ 
                duration: 1.5, 
                repeat: Infinity, 
                repeatType: "loop",
                delay: 0.6
              }}
            ></motion.span>
          </div>
        </motion.div>
      </motion.section>

      {/* Stats Section */}
      <div className="stats-section" ref={statsSectionRef}>
        <div className="container">
          <div className="stats-grid">
            <motion.div 
              initial={{ opacity: 0, y: 30 }}
              animate={statsInView ? { opacity: 1, y: 0 } : {}}
              transition={{ duration: 0.6 }}
              className="stat-card"
            >
              <div className="stat-icon">⏱️</div>
              <div className="stat-number count-up" ref={el => statsRef.current[0] = el} data-value="30">0%</div>
              <div className="stat-title">Faster Commutes</div>
            </motion.div>
            <motion.div 
              initial={{ opacity: 0, y: 30 }}
              animate={statsInView ? { opacity: 1, y: 0 } : {}}
              transition={{ duration: 0.6, delay: 0.2 }}
              className="stat-card"
            >
              <div className="stat-icon">🚗</div>
              <div className="stat-number count-up" ref={el => statsRef.current[1] = el} data-value="40">0%</div>
              <div className="stat-title">Less Congestion</div>
            </motion.div>
            <motion.div 
              initial={{ opacity: 0, y: 30 }}
              animate={statsInView ? { opacity: 1, y: 0 } : {}}
              transition={{ duration: 0.6, delay: 0.4 }}
              className="stat-card"
            >
              <div className="stat-icon">🌱</div>
              <div className="stat-number count-up" ref={el => statsRef.current[2] = el} data-value="25">0%</div>
              <div className="stat-title">CO₂ Reduction</div>
            </motion.div>
            <motion.div 
              initial={{ opacity: 0, y: 30 }}
              animate={statsInView ? { opacity: 1, y: 0 } : {}}
              transition={{ duration: 0.6, delay: 0.6 }}
              className="stat-card"
            >
              <div className="stat-icon">🛣️</div>
              <div className="stat-number count-up" ref={el => statsRef.current[3] = el} data-value="85">0%</div>
              <div className="stat-title">Traffic Flow Improvement</div>
            </motion.div>
          </div>
        </div>
      </div>
      
      {/* Features Section with Enhanced UI */}
      <div className="features-section" ref={featuresRef}>
        <div className="container">
          <motion.div 
            initial={{ opacity: 0, y: 30 }}
            animate={featuresInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.6 }}
            className="section-header"
          >
            <h2>Key Features</h2>
            <p>Innovative solutions powering our AI-driven traffic management system</p>
          </motion.div>
          <div className="feature-cards">
            {[0, 1, 2, 3].map(i => (
              <motion.div
                key={i}
                custom={i}
                variants={featureCardVariants}
                initial="hidden"
                animate={controls}
                whileHover={{ y: -15, boxShadow: "0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)" }}
                className="feature-card"
              >
                <motion.div 
                  whileHover={{ rotate: 5, scale: 1.1 }}
                  className="feature-icon-wrapper"
                >
                  <div className="feature-icon">
                    {i === 0 ? "🚦" : i === 1 ? "🔄" : i === 2 ? "🅿️" : "🛡️"}
                  </div>
                </motion.div>
                <h3>
                  {i === 0 ? "Real-Time Traffic Data" : 
                   i === 1 ? "Decentralized Network" : 
                   i === 2 ? "AI Parking Solution" : 
                   "Enhanced Safety"}
                </h3>
                <p>
                  {i === 0 ? "Instant V2V and V2I communication enables real-time data exchange for optimal traffic flow" : 
                   i === 1 ? "P2P network reducing dependency on centralized traffic control for resilient operations" : 
                   i === 2 ? "Intelligent parking management to minimize congestion caused by parking searches" : 
                   "Instant accident and hazard alerts to nearby vehicles to prevent cascade collisions"}
                </p>
                <motion.div 
                  initial={{ opacity: 0, height: 0 }}
                  whileHover={{ opacity: 1, height: "auto" }}
                  transition={{ duration: 0.3 }}
                  className="feature-hover"
                >
                  <ul className="feature-details">
                    {i === 0 ? (
                      <>
                        <li>Real-time traffic density monitoring</li>
                        <li>Instant accident reporting</li>
                        <li>Dynamic traffic signal control</li>
                      </>
                    ) : i === 1 ? (
                      <>
                        <li>Fault-tolerant architecture</li>
                        <li>Distributed computing</li>
                        <li>Self-organizing networks</li>
                      </>
                    ) : i === 2 ? (
                      <>
                        <li>Real-time parking availability</li>
                        <li>Predictive parking algorithms</li>
                        <li>Integrated payment systems</li>
                      </>
                    ) : (
                      <>
                        <li>Collision prevention system</li>
                        <li>Emergency vehicle priority</li>
                        <li>Road hazard notifications</li>
                      </>
                    )}
                  </ul>
                </motion.div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>

      {/* How It Works Section */}
      <div className="how-it-works-section" ref={processRef}>
        <div className="container">
          <motion.div 
            initial={{ opacity: 0, y: 30 }}
            animate={processInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.6 }}
            className="section-header"
          >
            <h2>How It Works</h2>
            <p>Understanding our innovative approach to traffic management</p>
          </motion.div>
          <div className="process-steps">
            {[
              { num: 1, title: "Data Collection", desc: "Vehicles and infrastructure constantly exchange traffic data via VANET technology" },
              { num: 2, title: "AI Processing", desc: "Our algorithms analyze traffic patterns and density in real-time" },
              { num: 3, title: "Optimization", desc: "Traffic signals are dynamically adjusted based on current conditions" },
              { num: 4, title: "Implementation", desc: "Vehicles receive optimal routing and timing information" }
            ].map((step, index) => (
              <React.Fragment key={index}>
                <motion.div 
                  initial={{ opacity: 0, y: 30 }}
                  className="process-step"
                >
                  <motion.div 
                    whileHover={{ scale: 1.1, rotate: 5 }}
                    className="step-number"
                  >
                    {step.num}
                  </motion.div>
                  <div className="step-content">
                    <h3>{step.title}</h3>
                    <p>{step.desc}</p>
                  </div>
                </motion.div>
                {index < 3 && (
                  <motion.div 
                    initial={{ width: 0 }}
                    className="process-connector"
                  ></motion.div>
                )}
              </React.Fragment>
            ))}
          </div>
        </div>
      </div>

      {/* Call to Action */}
      <div className="cta-section">
        <div className="container">
          <motion.div 
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.5 }}
            transition={{ duration: 0.6 }}
            className="cta-content"
          >
            <h2>Ready to revolutionize traffic management?</h2>
            <p>Experience the power of AI-driven traffic optimization</p>
            <motion.button 
              whileHover={{ scale: 1.05, boxShadow: "0 15px 25px -5px rgba(67, 97, 238, 0.4)" }}
              whileTap={{ scale: 0.95 }}
              className="cta-button-large" 
              onClick={() => setActiveTab('demo')}
            >
              Try the Demo
            </motion.button>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default HomePage; 