import React from 'react';
import { motion } from 'framer-motion';

const LoadingScreen = ({ progress }) => {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { 
        when: "beforeChildren",
        staggerChildren: 0.2
      }
    },
    exit: { 
      opacity: 0,
      transition: { 
        when: "afterChildren",
        staggerChildren: 0.1,
        staggerDirection: -1
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: { 
      y: 0, 
      opacity: 1,
      transition: { type: "spring", stiffness: 300 }
    },
    exit: { 
      y: -20, 
      opacity: 0 
    }
  };

  const progressVariants = {
    hidden: { width: "0%" },
    visible: { 
      width: `${progress}%`,
      transition: { duration: 0.5, ease: "easeInOut" }
    }
  };

  return (
    <motion.div 
      className="loading-screen"
      variants={containerVariants}
      initial="hidden"
      animate="visible"
      exit="exit"
    >
      <motion.div 
        className="loading-icon"
        animate={{ rotate: 360 }}
        transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
      >
        <motion.svg 
          width="60" 
          height="60" 
          viewBox="0 0 50 50"
          animate={{ 
            scale: [1, 1.1, 1],
            opacity: [0.8, 1, 0.8],
          }}
          transition={{ duration: 3, repeat: Infinity }}
        >
          <motion.path
            d="M25 5 L45 15 L45 35 L25 45 L5 35 L5 15 Z"
            fill="none"
            stroke="white"
            strokeWidth="1.5"
            animate={{ 
              strokeDasharray: ["0 100", "100 0"],
              rotate: [0, 360]
            }}
            transition={{ duration: 3, repeat: Infinity }}
          />
          <motion.circle 
            cx="25" 
            cy="25" 
            r="15" 
            fill="none" 
            stroke="white" 
            strokeWidth="1.5"
            animate={{ 
              strokeDasharray: ["0 100", "100 0"],
              rotate: [360, 0]
            }}
            transition={{ duration: 3, repeat: Infinity, delay: 0.5 }}
          />
          <motion.circle 
            cx="25" 
            cy="25" 
            r="8" 
            fill="white"
            animate={{ 
              r: [8, 10, 8],
              opacity: [0.8, 1, 0.8]
            }}
            transition={{ duration: 2, repeat: Infinity }}
          />
        </motion.svg>
      </motion.div>
      
      <motion.div className="loading-logo" variants={itemVariants}>
        Project DRIVE
      </motion.div>
      
      <motion.p variants={itemVariants}>
        Preparing your traffic management experience...
      </motion.p>
      
      <motion.div className="loading-progress-container" variants={itemVariants}>
        <motion.div 
          className="loading-progress-bar"
          variants={progressVariants}
          initial="hidden"
          animate="visible"
        />
        <motion.div 
          className="loading-progress-text"
          animate={{ opacity: [0.5, 1, 0.5] }}
          transition={{ duration: 1.5, repeat: Infinity }}
        >
          {progress}%
        </motion.div>
      </motion.div>
      
      <motion.div 
        className="loading-road"
        variants={itemVariants}
      >
        <motion.div 
          className="loading-car"
          animate={{ 
            x: [-40, window.innerWidth + 40],
          }}
          transition={{ 
            duration: 3, 
            repeat: Infinity,
            ease: "linear"
          }}
        />
        
        <motion.div className="loading-dash-container">
          {[...Array(20)].map((_, i) => (
            <motion.div 
              key={i} 
              className="loading-dash"
              animate={{ opacity: [0.3, 1, 0.3] }}
              transition={{ 
                duration: 1.5, 
                repeat: Infinity,
                delay: i * 0.05, 
                ease: "easeInOut"
              }}
            />
          ))}
        </motion.div>
      </motion.div>
    </motion.div>
  );
};

export default LoadingScreen; 