import React, { useState } from 'react';
import axios from 'axios';

const DemoPage = () => {
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);

  const handleFileChange = (e) => {
    // Convert FileList to array and set to state
    setSelectedFiles(Array.from(e.target.files));
  };

  const handleSubmit = async (e) => {
    setLoading(true);
    setUploadProgress(0);
    e.preventDefault();
    
    // Ensure exactly 4 files are selected
    if (selectedFiles.length !== 4) {
      alert('Please upload exactly 4 videos.');
      setLoading(false);
      return;
    }

    const formData = new FormData();
    // Append all selected files to FormData
    selectedFiles.forEach(file => formData.append('videos', file));

    try {
      const response = await axios.post('http://localhost:5000/upload', formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          setUploadProgress(percentCompleted);
        }
      });
      setResult(response.data);
      console.log(response);
      setLoading(false);
    } catch (error) {
      console.error('Error uploading files:', error);
      setLoading(false);
    }
  };

  return (
    <div className="demo-container">
      <h2>AI Traffic Light Optimization Demo</h2>
      <div className="demo-content">
        <div className="upload-section">
          <div className="upload-card">
            <h3>📹 Upload Your Traffic Videos</h3>
            <p>Select 4 videos showing different roads at an intersection. Our AI will analyze these videos to provide optimized traffic light timings.</p>
            <form onSubmit={handleSubmit}>
              <div className="file-upload-container">
                <label className="file-upload-label">
                  <input 
                    type="file" 
                    multiple 
                    accept="video/*" 
                    onChange={handleFileChange}
                    className="file-input"
                  />
                  <span className="upload-button-text">Choose Videos</span>
                </label>
                <span className="file-count">{selectedFiles.length} video(s) selected</span>
              </div>
              <button 
                type="submit" 
                className="process-button" 
                disabled={loading || selectedFiles.length !== 4}
              >
                {loading ? 'Processing...' : 'Analyze Traffic'}
              </button>
            </form>
            {loading && (
              <div className="progress-container">
                <div className="progress-bar" style={{width: `${uploadProgress}%`}}></div>
                <div className="progress-text">Uploading: {uploadProgress}%</div>
                <p className="processing-msg">Processing videos, please wait...</p>
              </div>
            )}
          </div>
        </div>
        
        <div className="results-section">
          <div className="results-card">
            {!loading && !result && (
              <div className="placeholder-container">
                <p className="placeholder">AI analysis results will appear here</p>
                <div className="traffic-lights">
                  <span className="traffic-light">🚦</span>
                  <span className="traffic-light">🚦</span>
                  <span className="traffic-light">🚦</span>
                  <span className="traffic-light">🚦</span>
                </div>
              </div>
            )}
            {result && !result.error && (
              <div className="results-content">
                <h3>✅ Optimization Results</h3>
                <p>Your traffic light timings have been optimized based on AI analysis:</p>
                <div className="timing-grid">
                  <div className="timing-card">
                    <div className="direction">North</div>
                    <div className="time-value">{result.north}</div>
                    <div className="time-unit">seconds</div>
                  </div>
                  <div className="timing-card">
                    <div className="direction">South</div>
                    <div className="time-value">{result.south}</div>
                    <div className="time-unit">seconds</div>
                  </div>
                  <div className="timing-card">
                    <div className="direction">East</div>
                    <div className="time-value">{result.east}</div>
                    <div className="time-unit">seconds</div>
                  </div>
                  <div className="timing-card">
                    <div className="direction">West</div>
                    <div className="time-value">{result.west}</div>
                    <div className="time-unit">seconds</div>
                  </div>
                </div>
                <div className="efficiency-metrics">
                  <h4>Efficiency Improvements</h4>
                  <div className="metrics-row">
                    <div className="metric">
                      <span className="metric-value">32%</span>
                      <span className="metric-label">Less Wait Time</span>
                    </div>
                    <div className="metric">
                      <span className="metric-value">28%</span>
                      <span className="metric-label">Reduced Congestion</span>
                    </div>
                    <div className="metric">
                      <span className="metric-value">41%</span>
                      <span className="metric-label">CO2 Reduction</span>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {result && result.error && (
              <div className="error-container">
                <h3>Error Occurred</h3>
                <p className="error-message">{result.error}</p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default DemoPage; 