import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const FeatureIcon = ({ icon, isActive }) => (
  <motion.div 
    className={`feature-icon-container ${isActive ? 'active' : ''}`}
    initial={{ scale: 0.8, opacity: 0.7 }}
    animate={{ 
      scale: isActive ? 1.1 : 1, 
      opacity: isActive ? 1 : 0.7,
      rotateY: isActive ? [0, 180, 360] : 0
    }}
    transition={{ 
      duration: isActive ? 0.8 : 0.3,
      type: "spring", 
      stiffness: 100 
    }}
  >
    <span className="feature-icon">{icon}</span>
    {isActive && (
      <motion.div 
        className="icon-glow"
        initial={{ scale: 0.5, opacity: 0 }}
        animate={{ scale: 1.5, opacity: 0.8 }}
        transition={{ duration: 1, repeat: Infinity, repeatType: "mirror" }}
      />
    )}
  </motion.div>
);

const FeaturesPage = () => {
  const [activeFeature, setActiveFeature] = useState('vanet');
  
  useEffect(() => {
    document.title = 'Features | Project DRIVE';
    
    // Auto rotate through features for visual effect
    const interval = setInterval(() => {
      setActiveFeature(current => {
        const currentIndex = features.findIndex(f => f.id === current);
        const nextIndex = (currentIndex + 1) % features.length;
        return features[nextIndex].id;
      });
    }, 8000);
    
    return () => clearInterval(interval);
  }, []);
  
  // Effect to animate background color transitions
  useEffect(() => {
    // Apply color transitions to the active feature
    const transitionElements = document.querySelectorAll('.color-transition');
    
    transitionElements.forEach(element => {
      element.style.transition = 'background-color 0.6s ease, color 0.6s ease, border-color 0.6s ease, box-shadow 0.6s ease';
    });
  }, []);
  
  // Animation variants with color transitions
  const pageVariants = {
    initial: { opacity: 0 },
    animate: { 
      opacity: 1,
      transition: { 
        duration: 0.6,
        when: "beforeChildren",
        staggerChildren: 0.1
      }
    },
    exit: { opacity: 0 }
  };
  
  const itemVariants = {
    initial: { y: 30, opacity: 0 },
    animate: { 
      y: 0, 
      opacity: 1,
      transition: { type: "spring", stiffness: 100 }
    }
  };
  
  const tabVariants = {
    inactive: { 
      opacity: 0.8,
      y: 0,
      scale: 0.95,
      boxShadow: "0px 5px 10px rgba(0, 0, 0, 0.1)"
    },
    active: { 
      opacity: 1,
      y: -5,
      scale: 1,
      boxShadow: "0px 15px 25px rgba(67, 97, 238, 0.3)"
    },
    hover: {
      scale: 1.03,
      y: -3,
      transition: { type: "spring", stiffness: 400 }
    }
  };
  
  const contentVariants = {
    initial: { opacity: 0, x: -20 },
    animate: { 
      opacity: 1, 
      x: 0,
      transition: { 
        type: "spring", 
        duration: 0.6,
        when: "beforeChildren",
        staggerChildren: 0.1
      }
    },
    exit: { opacity: 0, x: 20 }
  };
  
  const imageVariants = {
    initial: { opacity: 0, scale: 0.8, rotate: -5 },
    animate: { 
      opacity: 1, 
      scale: 1, 
      rotate: 0,
      transition: { type: "spring", duration: 0.8, bounce: 0.4 }
    },
    exit: { opacity: 0, scale: 0.8, rotate: 5 }
  };
  
  const listItemVariants = {
    initial: { opacity: 0, x: -20 },
    animate: { opacity: 1, x: 0 },
    hover: { x: 5, color: 'var(--primary-color)' }
  };

  // IntersectionObserver hooks for scroll animations
  const [heroRef, heroInView] = useInView({
    threshold: 0.2,
    triggerOnce: false
  });
  
  const [tabsRef, tabsInView] = useInView({
    threshold: 0.1,
    triggerOnce: false
  });
  
  const [integrationRef, integrationInView] = useInView({
    threshold: 0.2,
    triggerOnce: false
  });
  
  const [comparisonRef, comparisonInView] = useInView({
    threshold: 0.1,
    triggerOnce: false
  });

  const features = [
    {
      id: 'vanet',
      title: 'VANET Technology',
      icon: '📡',
      color: 'var(--primary-color)',
      description: 'Vehicular Ad Hoc Networks form the foundation of our system, enabling direct vehicle-to-vehicle and vehicle-to-infrastructure communication.'
    },
    {
      id: 'ai',
      title: 'AI-Powered Analysis',
      icon: '🧠',
      color: 'var(--indigo)',
      description: 'Advanced machine learning algorithms analyze traffic patterns in real-time to make intelligent decisions about traffic flow management.'
    },
    {
      id: 'p2p',
      title: 'Decentralized P2P Network',
      icon: '🔄',
      color: 'var(--teal)',
      description: 'Our peer-to-peer network architecture ensures system resilience, eliminating single points of failure and reducing dependency on central servers.'
    },
    {
      id: 'parking',
      title: 'Smart Parking Management',
      icon: '🅿️',
      color: 'var(--purple)',
      description: 'Real-time parking space availability information reduces time spent searching for parking, significantly reducing urban congestion.'
    },
    {
      id: 'safety',
      title: 'Enhanced Road Safety',
      icon: '🛡️',
      color: 'var(--rose)',
      description: 'Instant hazard notifications and accident alerts help prevent cascade collisions and improve overall road safety.'
    },
    {
      id: 'eco',
      title: 'Eco-Friendly Transportation',
      icon: '🌱',
      color: 'var(--success-color)',
      description: 'By reducing idle time and optimizing traffic flow, our system contributes to reduced fuel consumption and lower carbon emissions.'
    }
  ];

  const featureDetails = {
    'vanet': {
      title: 'VANET Technology',
      image: `${process.env.PUBLIC_URL}/assets/vanet-illustration.png`,
      color: 'var(--primary-color)',
      gradient: 'var(--gradient-primary)',
      description: 'Vehicular Ad Hoc Networks (VANETs) enable direct communication between vehicles and infrastructure without relying on fixed networks. This technology creates a mobile mesh network that adapts in real-time as vehicles move through the transportation system.',
      benefits: [
        'Real-time communication with minimal latency',
        'Works in areas with limited infrastructure',
        'Self-healing network with high reliability',
        'Scalable to accommodate growing traffic volumes'
      ],
      applications: [
        'Traffic congestion detection and avoidance',
        'Accident notification and emergency vehicle routing',
        'Dynamic traffic signal optimization',
        'Road condition monitoring and reporting'
      ]
    },
    'ai': {
      title: 'AI-Powered Analysis',
      image: '/logo192.png',
      color: 'var(--indigo)',
      gradient: 'var(--gradient-indigo-purple)',
      description: 'Our system employs advanced artificial intelligence and machine learning algorithms to process the vast amounts of data collected through the VANET. These algorithms identify patterns, predict traffic flows, and make intelligent decisions to optimize the entire traffic system.',
      benefits: [
        'Adaptive learning that improves over time',
        'Predictive analytics for proactive traffic management',
        'Computer vision for accurate vehicle detection and counting',
        'Natural language processing for voice-based traffic updates'
      ],
      applications: [
        'Traffic flow prediction and optimization',
        'Anomaly detection for unusual traffic patterns',
        'Personalized route recommendations',
        'Automated incident detection and response'
      ]
    },
    'p2p': {
      title: 'Decentralized P2P Network',
      image: '/logo192.png',
      color: 'var(--teal)',
      gradient: 'var(--gradient-teal)',
      description: 'The peer-to-peer architecture of our system creates a robust, decentralized network where each vehicle both contributes to and benefits from the shared intelligence of the network. This approach eliminates central bottlenecks and single points of failure.',
      benefits: [
        'No single point of failure in the system',
        'Enhanced data privacy and security',
        'Reduced infrastructure requirements',
        'Improved system resilience during network disruptions'
      ],
      applications: [
        'Distributed traffic computation',
        'Shared traffic intelligence across vehicles',
        'Local traffic management during infrastructure outages',
        'Secure data sharing between trusted participants'
      ]
    },
    'parking': {
      title: 'Smart Parking Management',
      image: '/logo192.png',
      color: 'var(--purple)',
      gradient: 'var(--gradient-purple)',
      description: 'Finding parking in urban areas accounts for a significant portion of traffic congestion. Our smart parking system provides real-time information about available parking spaces, reservation capabilities, and predictive availability based on historical patterns.',
      benefits: [
        'Reduces time spent searching for parking',
        'Decreases traffic congestion in urban centers',
        'Optimizes utilization of parking infrastructure',
        'Improves urban planning through parking usage analytics'
      ],
      applications: [
        'Real-time parking availability visualization',
        'Parking space reservation system',
        'Dynamic pricing based on demand',
        'Integrated payment systems for seamless experience'
      ]
    },
    'safety': {
      title: 'Enhanced Road Safety',
      image: '/logo192.png',
      color: 'var(--rose)',
      gradient: 'var(--gradient-error-rose)',
      description: 'Safety is a paramount concern in any traffic management system. Our technology provides immediate accident notifications, hazard alerts, and prioritized routing for emergency vehicles to create a safer transportation ecosystem.',
      benefits: [
        'Faster emergency response times',
        'Reduced secondary accident rates',
        'Improved awareness of potential hazards',
        'Prioritized safety for vulnerable road users'
      ],
      applications: [
        'Real-time accident notification system',
        'Road hazard detection and alerts',
        'Emergency vehicle preemption at intersections',
        'Pedestrian and cyclist safety features'
      ]
    },
    'eco': {
      title: 'Eco-Friendly Transportation',
      image: '/logo192.png',
      color: 'var(--success-color)',
      gradient: 'var(--gradient-success-accent)',
      description: 'By optimizing traffic flow, reducing congestion, and minimizing idle time, our system contributes significantly to environmental sustainability by reducing fuel consumption and lowering carbon emissions from the transportation sector.',
      benefits: [
        'Reduced fuel consumption through optimized routes',
        'Lower carbon emissions from decreased idling',
        'Promotion of eco-driving behaviors',
        'Support for sustainable transportation policies'
      ],
      applications: [
        'Eco-route calculation and recommendations',
        'Fuel efficiency monitoring and feedback',
        'Green wave traffic signal coordination',
        'Integration with electric vehicle charging infrastructure'
      ]
    }
  };
  
  // Find the active feature data
  const activeFeatureData = features.find(f => f.id === activeFeature);
  const activeFeatureDetail = featureDetails[activeFeature];

  return (
    <motion.div 
      className="features-page-container"
      variants={pageVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <div className="features-hero" ref={heroRef}>
        <motion.div 
          className="hero-background color-transition"
          animate={{ 
            backgroundPosition: ['0% 0%', '100% 100%'],
            opacity: heroInView ? 1 : 0
          }}
          transition={{ 
            duration: 20, 
            repeat: Infinity, 
            repeatType: "reverse",
            ease: "linear" 
          }}
        >
          <div className="hero-grid"></div>
        </motion.div>
        
        <motion.div className="feature-floating-shapes">
          <motion.div 
            className="floating-shape shape-1"
            animate={{
              x: [0, 30, 0],
              y: [0, 15, 0],
              opacity: [0.4, 0.5, 0.4]
            }}
            transition={{
              duration: 8,
              repeat: Infinity,
              repeatType: "reverse"
            }}
          />
          <motion.div 
            className="floating-shape shape-2"
            animate={{
              x: [0, -25, 0],
              y: [0, -20, 0],
              opacity: [0.4, 0.6, 0.4]
            }}
            transition={{
              duration: 9,
              repeat: Infinity,
              repeatType: "reverse"
            }}
          />
          <motion.div 
            className="floating-shape shape-3"
            animate={{
              x: [0, 15, 0],
              y: [0, -15, 0],
              opacity: [0.3, 0.5, 0.3]
            }}
            transition={{
              duration: 7,
              repeat: Infinity,
              repeatType: "reverse"
            }}
          />
          <motion.div 
            className="floating-shape shape-4"
            animate={{
              x: [0, -20, 0],
              y: [0, 10, 0],
              opacity: [0.3, 0.4, 0.3]
            }}
            transition={{
              duration: 10,
              repeat: Infinity,
              repeatType: "reverse"
            }}
          />
        </motion.div>
        
        <div className="container">
          <motion.div 
            className="features-page-header"
            variants={itemVariants}
            initial="initial"
            animate={heroInView ? "animate" : "initial"}
          >
            <h1>Advanced Features</h1>
            <div className="header-subtitle">Discover how Project DRIVE is revolutionizing traffic management</div>
            <motion.div 
              className="header-underline color-transition"
              initial={{ width: 0 }}
              animate={{ width: heroInView ? '150px' : 0 }}
              transition={{ duration: 0.8, delay: 0.3 }}
              style={{ background: 'var(--gradient-primary-secondary)' }}
            ></motion.div>
          </motion.div>
        </div>
      </div>
      
      <div className="features-content container">
        <div className="features-tabs-section" ref={tabsRef}>
          <motion.h2
            initial={{ opacity: 0, y: 20 }}
            animate={tabsInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
            transition={{ duration: 0.5 }}
          >
            Core Technologies
          </motion.h2>
          
          <motion.div 
            className="features-tabs"
            initial={{ opacity: 0 }}
            animate={tabsInView ? { opacity: 1 } : { opacity: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            {features.map((feature, index) => (
              <motion.div
                key={feature.id}
                className={`feature-tab color-transition ${activeFeature === feature.id ? 'active' : ''}`}
                variants={tabVariants}
                initial="inactive"
                animate={activeFeature === feature.id ? "active" : "inactive"}
                whileHover="hover"
                onClick={() => setActiveFeature(feature.id)}
                custom={index}
                style={{
                  backgroundColor: activeFeature === feature.id 
                    ? `rgba(${hexToRgb(feature.color)}, 0.1)` 
                    : 'white',
                  borderColor: activeFeature === feature.id 
                    ? feature.color 
                    : 'var(--medium-gray)'
                }}
              >
                <FeatureIcon icon={feature.icon} isActive={activeFeature === feature.id} />
                <div className="feature-tab-content">
                  <h3 
                    className="color-transition"
                    style={{ 
                      color: activeFeature === feature.id ? feature.color : 'var(--dark-color)'
                    }}
                  >{feature.title}</h3>
                  <p>{feature.description}</p>
                </div>
                {activeFeature === feature.id && (
                  <motion.div 
                    className="active-indicator color-transition"
                    layoutId="activeFeatureIndicator"
                    style={{ background: featureDetails[feature.id].gradient || feature.color }}
                  />
                )}
              </motion.div>
            ))}
          </motion.div>
        </div>
        
        <div className="feature-detail-section">
          <AnimatePresence mode="wait">
            <motion.div 
              key={activeFeature}
              className="feature-detail-content color-transition"
              variants={contentVariants}
              initial="initial"
              animate="animate"
              exit="exit"
              style={{
                backgroundImage: `radial-gradient(circle at 80% 20%, rgba(${hexToRgb(activeFeatureData.color)}, 0.1) 0%, transparent 70%)`
              }}
            >
              <div className="feature-detail-grid">
                <div className="feature-detail-text">
                  <motion.h2 
                    className="color-transition"
                    variants={itemVariants}
                    style={{ 
                      background: activeFeatureDetail.gradient || activeFeatureData.color,
                      WebkitBackgroundClip: "text",
                      WebkitTextFillColor: "transparent"
                    }}
                  >
                    {activeFeatureData.title}
                  </motion.h2>
                  
                  <motion.p 
                    className="feature-description"
                    variants={itemVariants}
                  >
                    {activeFeatureDetail?.description || ''}
                  </motion.p>
                  
                  <motion.div className="feature-benefits" variants={itemVariants}>
                    <h3>Key Benefits</h3>
                    <ul className="benefits-list">
                      {activeFeatureDetail?.benefits?.map((benefit, index) => (
                        <motion.li 
                          key={index}
                          variants={listItemVariants}
                          whileHover="hover"
                          custom={index}
                        >
                          <span 
                            className="benefit-icon color-transition"
                            style={{ color: activeFeatureData.color }}
                          >✓</span>
                          {benefit}
                        </motion.li>
                      ))}
                    </ul>
                  </motion.div>
                  
                  <motion.div className="feature-applications" variants={itemVariants}>
                    <h3>Applications</h3>
                    <ul className="applications-list">
                      {activeFeatureDetail?.applications?.map((application, index) => (
                        <motion.li 
                          key={index}
                          variants={listItemVariants}
                          whileHover="hover"
                          custom={index}
                        >
                          <span 
                            className="application-icon color-transition"
                            style={{ color: activeFeatureData.color }}
                          >●</span>
                          {application}
                        </motion.li>
                      ))}
                    </ul>
                  </motion.div>
                </div>
                
                <motion.div 
                  className="feature-detail-visual"
                  variants={imageVariants}
                >
                  <div 
                    className="feature-visual-container color-transition" 
                    style={{ 
                      borderColor: activeFeatureData.color,
                      boxShadow: `0 15px 25px -5px rgba(${hexToRgb(activeFeatureData.color)}, 0.15)`
                    }}
                  >
                    <div className="feature-visual-content">
                      <div className="feature-icon-large">{activeFeatureData.icon}</div>
                      <div className="feature-animation-container">
                        <motion.div 
                          className="animation-circle color-transition"
                          style={{ borderColor: activeFeatureData.color }}
                          animate={{ 
                            scale: [1, 1.2, 1],
                            opacity: [0.7, 1, 0.7],
                            rotate: [0, 180]
                          }}
                          transition={{ 
                            duration: 3,
                            repeat: Infinity,
                            repeatType: "reverse"
                          }}
                        ></motion.div>
                        
                        <motion.div 
                          className="animation-circle outer color-transition"
                          style={{ borderColor: activeFeatureData.color }}
                          animate={{ 
                            scale: [1, 1.3, 1],
                            opacity: [0.5, 0.8, 0.5],
                            rotate: [0, -180]
                          }}
                          transition={{ 
                            duration: 4,
                            repeat: Infinity,
                            repeatType: "reverse"
                          }}
                        ></motion.div>
                      </div>
                    </div>
                    
                    <motion.div 
                      className="background-pattern color-transition"
                      style={{ 
                        position: 'absolute', 
                        inset: 0, 
                        opacity: 0.05,
                        background: `radial-gradient(circle at 30% 70%, ${activeFeatureData.color} 0%, transparent 50%),
                                   radial-gradient(circle at 70% 30%, ${activeFeatureData.color} 0%, transparent 50%)`
                      }}
                    />
                  </div>
                  
                  <motion.div 
                    className="tech-dots"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ delay: 0.5, duration: 0.5 }}
                  >
                    {[...Array(8)].map((_, i) => (
                      <motion.div 
                        key={i}
                        className="tech-dot color-transition"
                        style={{ 
                          backgroundColor: activeFeatureData.color,
                          boxShadow: `0 0 15px rgba(${hexToRgb(activeFeatureData.color)}, 0.6)`
                        }}
                        initial={{ scale: 0, opacity: 0 }}
                        animate={{ 
                          scale: 1, 
                          opacity: [0.3, 1, 0.3]
                        }}
                        transition={{ 
                          delay: i * 0.1, 
                          duration: 2,
                          repeat: Infinity,
                          repeatType: "reverse"
                        }}
                      />
                    ))}
                  </motion.div>
                </motion.div>
              </div>
              
              <motion.div 
                className="feature-action"
                variants={itemVariants}
              >
                <motion.button 
                  className="explore-button color-transition"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  style={{ 
                    background: activeFeatureDetail.gradient || activeFeatureData.color,
                    boxShadow: `0 8px 15px rgba(${hexToRgb(activeFeatureData.color)}, 0.3)`
                  }}
                >
                  Explore {activeFeatureData.title}
                </motion.button>
              </motion.div>
            </motion.div>
          </AnimatePresence>
        </div>
        
        {/* Color Palette Showcase */}
        <motion.div 
          className="color-palette-section"
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.2 }}
          transition={{ duration: 0.8 }}
        >
          <h2>Color Palette</h2>
          <p className="palette-description">Our design system uses a carefully curated color palette to represent different aspects of the DRIVE platform</p>
          
          <div className="color-grid">
            {[
              { name: 'Primary', var: 'var(--primary-color)', desc: 'Main brand color' },
              { name: 'Secondary', var: 'var(--secondary-color)', desc: 'Complementary accent' },
              { name: 'Accent', var: 'var(--accent-color)', desc: 'Highlighting elements' },
              { name: 'Success', var: 'var(--success-color)', desc: 'Positive actions' },
              { name: 'Warning', var: 'var(--warning-color)', desc: 'Caution indicators' },
              { name: 'Error', var: 'var(--rose)', desc: 'Critical alerts' },
              { name: 'Purple', var: 'var(--purple)', desc: 'Smart features' },
              { name: 'Teal', var: 'var(--teal)', desc: 'Data visualization' }
            ].map((color, index) => (
              <motion.div 
                key={index} 
                className="color-card"
                initial={{ opacity: 0, scale: 0.9 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                transition={{ delay: index * 0.1, duration: 0.5 }}
                whileHover={{ 
                  y: -10, 
                  boxShadow: `0 20px 25px -5px rgba(${hexToRgb(color.var)}, 0.3)` 
                }}
              >
                <div 
                  className="color-preview" 
                  style={{ backgroundColor: color.var }}
                ></div>
                <div className="color-info">
                  <h3>{color.name}</h3>
                  <p>{color.desc}</p>
                </div>
              </motion.div>
            ))}
          </div>
        </motion.div>
      </div>
    </motion.div>
  );
};

// Helper function to convert hex color to RGB
function hexToRgb(hex) {
  // Handle CSS variables
  if (hex.startsWith('var(--')) {
    // Extract variable name and get the computed value
    const varName = hex.substring(4, hex.length - 1);
    const computedStyle = getComputedStyle(document.documentElement);
    hex = computedStyle.getPropertyValue(varName).trim();
  }

  // Remove # if present
  hex = hex.replace('#', '');
  
  // Parse the hex values
  const r = parseInt(hex.substring(0, 2), 16) || 67;
  const g = parseInt(hex.substring(2, 4), 16) || 97;
  const b = parseInt(hex.substring(4, 6), 16) || 238;
  
  return `${r}, ${g}, ${b}`;
}

export default FeaturesPage; 