import React, { useState, useContext } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { LogoContext } from '../App';

const LogoUploader = () => {
  const [isPanelOpen, setIsPanelOpen] = useState(false);
  const [selectedFile, setSelectedFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState(null);
  const { currentLogo, updateLogo } = useContext(LogoContext);

  const togglePanel = () => {
    setIsPanelOpen(!isPanelOpen);
    // Reset state when closing panel
    if (isPanelOpen) {
      setSelectedFile(null);
      setPreviewUrl(null);
    }
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file && file.type.match('image.*')) {
      setSelectedFile(file);
      
      // Create preview URL
      const reader = new FileReader();
      reader.onload = () => {
        setPreviewUrl(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleUpload = () => {
    if (previewUrl) {
      updateLogo(previewUrl);
      setIsPanelOpen(false);
      setSelectedFile(null);
      setPreviewUrl(null);
    }
  };

  const handleCancel = () => {
    setSelectedFile(null);
    setPreviewUrl(null);
  };

  const selectSampleLogo = (logoPath) => {
    updateLogo(`${process.env.PUBLIC_URL}/assets/${logoPath}`);
    setIsPanelOpen(false);
  };

  // Animation variants
  const panelVariants = {
    hidden: { opacity: 0, y: 20, scale: 0.9 },
    visible: { 
      opacity: 1, 
      y: 0, 
      scale: 1,
      transition: {
        duration: 0.3,
        ease: "easeOut"
      }
    },
    exit: { 
      opacity: 0, 
      y: 20, 
      scale: 0.9,
      transition: {
        duration: 0.2,
        ease: "easeIn"
      }
    }
  };

  const buttonVariants = {
    hover: { 
      scale: 1.05,
      boxShadow: "0 5px 15px rgba(0, 0, 0, 0.1)"
    },
    tap: { 
      scale: 0.95 
    }
  };

  return (
    <div className="logo-uploader">
      <motion.button 
        className="uploader-toggle"
        onClick={togglePanel}
        variants={buttonVariants}
        whileHover="hover"
        whileTap="tap"
      >
        <span className="uploader-icon">🖼️</span>
        Change Logo
      </motion.button>

      <AnimatePresence>
        {isPanelOpen && (
          <motion.div 
            className="uploader-panel"
            variants={panelVariants}
            initial="hidden"
            animate="visible"
            exit="exit"
          >
            <h3 className="uploader-title">Select a Logo</h3>
            
            <div className="sample-logos">
              <motion.div 
                className="sample-logo"
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                onClick={() => selectSampleLogo('drivelogo.jpg')}
              >
                <img src={`${process.env.PUBLIC_URL}/assets/drivelogo.jpg`} alt="Drive Logo" />
              </motion.div>
              
              <motion.div 
                className="sample-logo"
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                onClick={() => selectSampleLogo('impactlogo.jpg')}
              >
                <img src={`${process.env.PUBLIC_URL}/assets/impactlogo.jpg`} alt="Impact Logo" />
              </motion.div>
            </div>
            
            <div className="uploader-divider">
              <span>OR</span>
            </div>
            
            <div className="custom-upload">
              <label className="upload-button">
                Upload Your Logo
                <input 
                  type="file" 
                  accept="image/*"
                  onChange={handleFileChange}
                  style={{ display: 'none' }}
                />
              </label>
            </div>
            
            {previewUrl && (
              <div className="preview-container">
                <h4>Logo Preview</h4>
                <div className="logo-preview">
                  <img src={previewUrl} alt="Logo Preview" />
                </div>
                
                <div className="preview-actions">
                  <motion.button 
                    className="upload-action"
                    onClick={handleUpload}
                    variants={buttonVariants}
                    whileHover="hover"
                    whileTap="tap"
                  >
                    Apply Logo
                  </motion.button>
                  
                  <motion.button 
                    className="cancel-action"
                    onClick={handleCancel}
                    variants={buttonVariants}
                    whileHover="hover"
                    whileTap="tap"
                  >
                    Cancel
                  </motion.button>
                </div>
              </div>
            )}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default LogoUploader; 